body {
  margin: 0;
  padding: 0;
}

#root {
  height: 100%;
}

/* 移动端适配 */
@media screen and (max-width: 768px) {
  .ant-layout-sider {
    position: absolute !important;
    z-index: 100;
    height: 100%;
  }
  
  .ant-layout-sider-collapsed {
    transform: translateX(-100%);
  }
}

/* 滚动条样式 */
::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 3px;
}

::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 3px;
}

::-webkit-scrollbar-thumb:hover {
  background: #555;
} 